.title {

    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.4;

    color: var(--color-dark);

    margin-bottom: 32px;
    max-width: 608px;
}

.summarySection {
    margin: 0 -15px;
    background-color: var(--bg-color);
}

@media screen and (min-width: 768px) {
    .title {
        margin-bottom: 68px;
        font-size: 34px;
        line-height: 1.2;
    }
}


@media screen and (max-width: 767px) {
    .section {
        padding-top: 32px;
        padding-bottom: 31px;
    }

    .title {
        max-width: 500px;
        margin: 0 auto;
        padding-bottom: 32px;
    }
}

@media screen and (min-width: 768px) {
    .section {
        padding-top: 160px;
        padding-bottom: 48px;
    }
}

@media screen and (min-width: 1280px) {
    .section {
        padding-top: 67px;
        padding-bottom: 111px;
    }

    .summarySection {
        margin: 0;
        background-color: inherit;
    }

    .content{
        display: grid;
        grid-template-columns: 3fr 2fr;        
        margin: 0 auto;
    }

    .wrapper {
        position: relative;
    }

    .leftSide {
        display: flex;
        flex-direction: column;
        padding-left: 15px;
    }

    .rightSide {
        max-width: 550px;
        padding: 0 15px;   
    }
}

@media screen and (max-width: 767px) {
    .leftSide {
        padding-left: 15px;
    }

    .layer {
        display: none;
    }

    .tabletLayer {
        display: none;
    }
}

@media screen and (min-width: 1280px) {

    .layer {
        position: absolute;
        
        bottom: 0;
        right: 0;
    }

    .tabletLayer {
        display: none;
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .leftSide {
        padding-left: 15px;
    }

    .layer {
        display: none;
    }

    .tabletLayer {
        position: absolute;
        top: 672px;
        bottom: 0;
        right: 0;
    }
}